// ()=>     fat arrow functions  funciones de flecha gorda ;
import {MdDone, MdDelete} from 'react-icons/md';

function TodoListItem({descripcion, id, completed, doneHandler, deleteHandler}){
    const onClick = (e)=>{
        doneHandler(id);
    }

    const onDeleteClick = (e)=>{
        deleteHandler(id);
    }

    const myClass = (completed)?"striked": "";
    return(
        /*<section className="flex justify-between max-w-lg w-screen items-center px-8 border bg-pink-50 bg-opacity-100 shadow-md rounded-lg m-auto">*/
        <li className="flex text-md  max-w-lg items-center h-16 px-8 m-auto border rounded-lg  bg-pink-50 bg-opacity-100 justify-between ">
            <span className={myClass}>{descripcion}</span>
            <span className="flex">
            <MdDone size={"2rem"} onClick={onClick}></MdDone>
            <MdDelete size={"2rem"} onClick={onDeleteClick}></MdDelete>
            </span>
        </li>
    );
}

function TodoList({todos, doneHandler, deleteHandler}){
    const TodoItems = todos.map((o)=>{
        return(
            <TodoListItem
                key={o.doneHandler}
                descripcion={o.description}
                id={o.id}
                completed={o.completed}
                doneHandler={doneHandler}
                deleteHandler={deleteHandler}
                >
            </TodoListItem>
        )
    });

    return(
        <section className="TodoList ">
            <ul>
                {TodoItems}
            </ul>
        </section>
    );
}

export default TodoList;