import FormField from '../../Forms/FormField';
import {MdAdd} from 'react-icons/md';

function NewTodo ({onChange,value,onAddNew}){
    return(
        <section className="TodoAddNew">
            <section className="flex justify-between w-screen items-end px-8 border align-middle bg-pink-50 bg-opacity-100 shadow-md rounded-lg max-w-lg m-auto">
                <FormField
                    type="text"
                    id="txtNTodo"
                    fname="txtNTodo"
                    onChange={onChange}
                    value={value}
                    caption="Nuevo Todo"
                    className="flex text-sm p-2 ml-16 m-2 w-max bg-white mt-1 border-none h-11 rounded-xl shadow-lg hover:bg-pink-100 focus:bg-pink-100 focus:ring-0"
                    labelClassName="flex w-5/6 mt-3"
                    containerClassName="flex"
                    placeholder="Realizar compras..."
                    onKeyUp = {(e)=> {if(e.keyCode == 13){onAddNew(e);}}}
                ></FormField>
                <MdAdd size={"3rem"} onClick={onAddNew}></MdAdd>
            </section>
        </section>
    );
}

export default NewTodo;